<template>
	<view class="user-info userInfo">
		<m-back-bar>
			<block slot="barTitle">{{$i18nMsg('lang-userinfo-title')}}</block><!--中文：个人信息编辑-->
			<block slot="barRight">
				<m-button
					@elementClick="handleTopClick(btn.type,btn.relationTabId)">
					<block slot="buttonIcon" style="display: inline-block;">
						<m-font-icon :iconSize="40"></m-font-icon>
					</block>
				</m-button>
			</block>
		</m-back-bar>
		<view class="user-info-list">
			<view class="user-info-item">
				<view>{{$i18nMsg('lang-userinfo-header')}}</view><!--中文：头像-->
				<!-- <image src="../../static/images/plantingDefault.png" mode="aspectFill" class="workbench-headerimg" @onUpImg="upBasicData" @onImgDel="delImgInfo" ref="uImage"></image> -->
				<!-- 图片上传-->
				<view class="workbench-headerimg">
					<!-- #ifdef MP -->
					<open-data class="user-img" type="userAvatarUrl"></open-data>
					<!-- #endif --> 
					<!-- #ifndef MP -->
					<m-upload
						:uploadNum="-1"
						:element = "element"
						:base64Url="currentFormData | formValue()"
						:requestConfig = "requestConfig()"
						:imageConfig = "element"
						:newUserImgPath = "userImgPath"
						:userImgPath = "userImage || picpath"
						uploadType = "touxiang"
						@onUpImg="upBasicData" 
						@allImgs="elementValueChang">
					</m-upload>
					
					<!-- #endif --> 
				</view>
			</view>
			<view class="user-info-item">
				<view>{{$i18nMsg('lang-myself-PhoneNumber')}}</view>
				<input class="uni-input" :placeholder="$i18nMsg('lang-myself-PhoneNumber')" v-model="tel" value="" />
			</view>
			<view class="user-info-item">
				<view>{{$i18nMsg('lang-myself-Email')}}</view>
				<input class="uni-input" :placeholder="$i18nMsg('lang-myself-Email')" v-model="email" value="" />
			</view>
			<view class="user-info-item">
				<view>{{$i18nMsg('lang-userinfo-signature')}}</view><!--中文：个人签名-->
				<input class="uni-input" :placeholder="$i18nMsg('lang-userinfo-please-signature')" v-model="personalSignal" value="" /><!--中文：请输入个人签名-->
			</view>
		</view>
		<view class="save-item">
			<m-button :btnTilte="$i18nMsg('lang-save')" btnClass="mainColor" @elementClick="insertDate(UpdateDate)"></m-button>
		</view>
	</view>
</template>

<script>
	// import mUpload from '@/components/plugin/uploadImgs/sunui-upimg-basic.vue'
	import { mapState, mapMutations } from 'vuex'
	import defaultImage from '@/static/images/default_image.png'
	import state from "@/store/state.js"
	import $http from '@/common/utils/request/index.js'
	import store from '@/store/index'
	import {fileApi} from '@/common/request/api.js'
	import mUpload from '@/components/mniUpload/MniUpload.vue'
	import EncryptionPSW from '@/static/js/encryptionPSW.js'
	export default {
		components: {
			mUpload
		},
		data() {
			return {
				tel:state.telephone,
				email:state.email,
				personalSignal:state.personalSignal,
				upload_picture_list:[
					
				],
				tableName:'user',
				userId:state.userId,
				token:state.token,
				picpath: defaultImage,
				typeId:1,	//头像图片
				fileIds:[],//图片参数
				upImgBasic: {
					basicConfig: {
						url: 'file/uploadFiles'
					},
					notli: true,
					sizeType: true,
					count:1,
					isAddImage: false,
					isDelIcon: false,
				},
				isUploading:true,
				isExist:0,
				userImgPath:'',
				userImgPathId:'',
				element: {
					visiabled:true
				},
				currentFormData:{},
				uploadPics: [],
			}
		},
		computed:{
			...mapState(["userImage"]),
		},
		filters: {
			formValue(formData,element={},type=2){//type 1-显示id，2-显示name,3-返回对象
				const elementId = element.elementId || ""
				let value = formData[elementId] || ""
				try{value = JSON.parse(value)
				}catch(e){}
				if(type === 1) value = typeof value !== "object" ? value : value.id || ""
				else if(type===3) value = value || {}
				else value = typeof value !== "object" ? value : value.name || ""
				//如果字段类型为4：decimal去除后面无用的点
				if(element.relationColumnType === 4 && value) value = COMMON_FORMAT_DECIMAL(value)
				return value
			}
		},
		methods: {
			async upBasicData(e) {
				const _this = this
				const uploadPics = e.uploadPics,
					  otherParam = e.getPicParam
				// 上传图片但未绑定的图片数组
				this.uploadPics = uploadPics;
				this.userImgPath = uploadPics[0].url;
				store.commit("UPDATE_USERIMAGE_STATE",this.uploadPics[0].url);
				let arrImg = [];
				for (let i = 0, len = uploadPics.length; i < len; i++) {
					try {
						if (uploadPics[i].id != "") {// 将未上传的图片信息保存到fileIds数组
							const fileId = uploadPics[i].id
							this.fileIds[fileId] = {
								typeId:otherParam.relationTypeId,
								stateId:otherParam.relationStateId,
								priorityId:otherParam.relationpriorityId,
							}
						}
					} catch (err) {
						console.log('上传失败...');
					}
				}
			},
			imageConfig(){
				return function(element = {}){
					return {
						 allImgsParam:{
							element:element,
							triggerBaseType:'upImg'
						 }
					}
				}
			},
			requestConfig(){
				let submitUrl ='file/uploadFiles'
				return {
					 submitUrl:'file/uploadFiles',
					 submitParam:EncryptionPSW({tableName:'user'}),
					 getPicParam:{
						relationId:this.userId,
						relationTableName:'user',
						relationTypeId:1
					 }
				}
			},
			//表单控件值发生改变
			async elementValueChang(param){
				if(param.value.length>0){
					this.isExist = 1;
					this.userImgPath = param.value[0].url;
					this.userImgPathId = param.value[0].id;
					store.commit("UPDATE_USERIMAGE_STATE",param.value[0].url);
				}
			},
			handleTopClick(){//处理顶部按钮的点击事件
				let url = '/pages/home/home?pageType=myself';
				uni.redirectTo({
					url:url
				})
			},
			insertDate(callback){//保存修改信息、头像
				let _this = this;
				if(this.uploadPics.length == 0){
					callback(_this);
					return;
				}
				_this.isUploading = true;
				let imgUrl = 'filesrelations/insert',
					obj = {
						mainId:this.uploadPics[0].id || null,
						relationTableName:this.tableName,
						relationId:this.userId,
						relationTypeId:this.typeId
					};
				if(_this.isExist == 1){
					imgUrl = 'filesrelations/update';
					obj = {
						mainId:this.uploadPics[0].id || null,
						relationTableName:this.tableName,
						relationId:this.userId,
						relationTypeId:this.typeId,
						id:this.userImgPathId,
					}
				}
				this.$http.post(imgUrl, obj)
				.then(res => {
					const result = res.data
					console.log(res)
					if(result.success){
						if(_this.isExist != 1){
							_this.userImgPathId = pics[0].id;
							_this.isExist = 1;
						}
						console.log(_this.userImgPath)
						store.commit("UPDATE_USERIMAGE_STATE",_this.userImgPath);
						callback(_this);
					}else{
						uni.showToast({
							title: _this.$i18nMsg('lang-myself-PictureUploadFailed'),// 中文：'图片上传失败',
							duration: 1000
						});
						_this.isUploading = false;
					}
					
				})
				.catch(err => {
					console.log(JSON.stringify(err))
				});
			},
			UpdateDate(_this){//保存修改信息、头像
				_this.$http.post('user/update', {email:_this.email,telephone:_this.tel,id:_this.userId,personalSignal:_this.personalSignal})
				.then(res => {
					const result = res.data
					if(result.success){
						uni.showToast({
							title: _this.$i18nMsg('lang-myself-ModifiedSuccess'),
							duration: 1000,
							icon:'none'
						});
						store.commit("UPDATE_USER_STATE",res.data.data);
						_this.isUploading = false;
					}else{
						uni.showToast({
							title: _this.$i18nMsg('lang-userinfo-faile'),// 中文：'修改失败',
							duration: 1000,
							icon:'none'
						});
						_this.isUploading = false;
					}
					
				})
				.catch(err => {
					console.log(JSON.stringify(err))
				})
			}
		}
	}
</script>

